.weather-app {
    width: 100%;
    height: 100vh;
    background-color: $color-lighter-blue;

    @include flex(row, center, center);

    &__title {
        font-size: calc(24px + 30 * ((100vw - 320px) / (1920 - 320)));
        text-transform: uppercase;
        color: var(--color-white);
        text-align: center;
        padding: 1rem 0rem;
        color: $color-white;
    }

    &__body {
        width: 30rem;
        background-color: $color-lighter-blue-2;
        filter: drop-shadow(0 0 0.75rem $color-black2);
        border-radius: 1.2rem;

        @media (min-width: 25em) {
            width: 40rem;
        }

        @media (min-width: 48em) {
            width: 50rem;
        }
    }

    /* Header-top*/

    &__header {
        position: relative;
    }

    &__img {}

    &__search-input {
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;

        @media (min-width: 48em) {
            top: 63%;
        }
    }

    &__search-input-box {}

    &__city-search {
        width: 24rem;
        border: none;
        background-color: transparent;
        border-bottom: 3px solid $color-dark-blue;
        border-radius: 1rem;
        font-family: inherit;
        font-weight: 700;
        color: $color-dark-blue;
        padding: 1rem 1rem .8rem 1rem;
        text-transform: uppercase;
        margin-bottom: .3rem;
        text-align: center;
        transition: all .3s ease-in-out;

        @media (min-width: 48em) {
            width: 34rem;
        }

        &::placeholder {
            font-size: 2rem;
            color: $color-dark-blue;
        }
    }

    &__city-search-label {
        display: block;
        font-size: 1.5rem;
        font-weight: 700;
        color: $color-dark-blue;
        text-align: center;
        transition: all .3s ease-in-out;
        margin-bottom: 1rem;
    }

    &__city-search:placeholder-shown + &__city-search-label {
        opacity: 0;
        visibility: hidden;
        transform: translateY(-4rem);
    }

    &__search-input-btn {
        display: inline-block;
        border: none;
        font-weight: 700;
        background-color: $color-dark-blue;
        padding: 1rem 2rem;
        border-radius: 1rem;
        text-transform: uppercase;
        color: $color-white;
        transition: all .3s ease-in-out;
        cursor: pointer;

        &:hover {
            background-color: $color-lighter-blue;
        }

        @media (min-width: 48em) {
            font-size: 1.8rem;
            padding: 1.3rem 2.4rem;
        }
    }

